// api/active-user.js

import axios from 'axios'

const API_URL = 'http://jasdig.com:5154/apron/api'

export function url_login(userData) {
  return axios.post(`${API_URL}/login`, userData)
}
export function url_logout(userData) {
  return axios.post(`${API_URL}/logout`, userData)
}

